<template>
<div class="container px-4">
    <table class="table w-full table-auto">
        <thead>
            <tr>
                <th>Nombre</th>
                <th>Apellido Paterno</th>
                <th>Apellido Materno</th>
                <th>Telefono Fijo</th>
                <th>Celular</th>
                <th>Correo</th>
                <th>Cedula Profesional</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="(item,index) in usuarios" :key="index">
                <td scope="row">{{item.nombre_usuario}}</td>
                <td>{{item.apellido_1}}</td>
                <td>{{item.apellido_2}}</td>
                <td>{{item.telefono_fijo}}</td>
                <td>{{item.celular}}</td>
                <td>{{item.correo}}</td>
                <td>{{item.cedula_profesional}}</td>
            </tr>
        </tbody>
    </table>
    <br>
    <router-link to="nuevoUsuario"><button class="bg-blue-500 text-white">Registrar Usuario</button></router-link>
</div>
</template>

<script>

export default{
    data() {
        return{
            usuarios: []
        }
    },
    created(){
        this.mostrarUsuarios();
    },
    methods:{
        mostrarUsuarios(){
            this.axios.get('/usuarios')
            .then(res => {
                console.log(res.data);
                this.usuarios = res.data;
            })
            .catch(e=>{
                console.log(e);
            })
        }
    }
}
</script>
