<template>
<div class="mt-8 mx-72 w-full max-w-sm flex">
    <div class="bg-gray-200 py-5 px-6 rounded-lg">
        <form class="space-y-5" @submit.prevent="agregarUsuario()">
            <h1>Nuevo Usuario</h1>
            <div class="flex">
                <div class="divCampo">
                    <input type="text" id="nombre_usuario" placeholder="Nombre" v-model="usuario.nombre_usuario">
                </div>
                <div class="divCampo">
                    <input type="text" id="apellido_1" placeholder="Apellido Paterno" v-model="usuario.apellido_1">
                </div>
                <div class="divCampo">
                    <input type="text" id="apellido_2" placeholder="Apellido Materno" v-model="usuario.apellido_2">
                </div>
            </div>

            <div class="flex">
                <div class="divCampo">
                    <input type="text" placeholder="domicilio" v-model="usuario.domicilio">
                </div>
                <div class="divCampo">
                    <input type="text" placeholder="telefono fijo" v-model="usuario.telefono_fijo">
                </div>
                <div class="divCampo">
                    <input type="text" placeholder="celular" v-model="usuario.celular">
                </div>
            </div>
            <div class="flex">
                <div class="divCampo">
                    <input type="text" placeholder="correo" v-model="usuario.correo">
                </div>
                <div class="divCampo">
                    <label for="fechaR" class=" px-2">Fecha de Registro</label>
                    <input type="date" id="fechaR" name="fechaR" placeholder="fecha de registro" v-model="usuario.fecha_registro">
                </div>
            </div>

            <div class="flex">
                <div class="divCampo"><input type="text" placeholder="municipio" v-model="usuario.municipio"></div>
                <div class="divCampo"><input type="text" placeholder="estado" v-model="usuario.estado"></div>
                <div class="divCampo"><input type="text" placeholder="pais" v-model="usuario.pais"></div>
            </div>

            <div class="flex">
                <div class="divCampo"><input type="password" placeholder="contraseña" v-model="usuario.password"></div>
                <div class="divCampo"><input type="text" placeholder="tipo de usuario" v-model="usuario.tipousuario"></div>
                <div class="divCampo"><input type="text" placeholder="cedula profesional" v-model="usuario.cedula_profesional"></div>
            </div>
            <button type="submit" class="bg-blue-500 text-white rounded-lg px-2 py-2">Registrar</button>
        </form>
    </div>
</div>
</template>

<script>
export default {
    data(){
        return{
            usuarios: [],
            usuario: {
            nombre_usuario:'',
            apellido_1:'',
            apellido_2:'',
            domicilio:'',
            telefono_fijo:'',
            celular:'',
            correo:'',
            fecha_registro:'',
            municipio:'',
            estado:'',
            pais:'',
            password:'',
            tipousuario:'',
            cedula_profesional:''
            }
        }
    },
    methods: {
        agregarUsuario(){
            console.log(this.usuario);
            this.axios.post('/nuevoUsuario',this.usuario)
            .then(res=>{
                this.usuarios.push(res.data);
                this.usuario.nombre_usuario = '';
                this.usuario.apellido_1 = '';
                this.usuario.apellido_2 = '';
                this.usuario.domicilio = '';
                this.usuario.telefono_fijo = '';
                this.usuario.celular = '';
                this.usuario.correo = '';
                this.usuario.fecha_registro = '';
                this.usuario.municipio = '';
                this.usuario.estado = '';
                this.usuario.pais = '';
                this.usuario.password = '';
                this.usuario.tipousuario = '';
                this.usuario.cedula_profesional = '';
            })
            .catch(e => {
                console.log(e.response);
            })
        }
    }
}
</script>

<style scoped>
div input{
    @apply py-1 px-2 shadow rounded-md;
}

.divCampo{
    @apply mt-1 px-2;
}
</style>